.project-card {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  transition: 250ms;

  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.project-card:hover {
  transform: scale(1.03);
}

.project-card img {
  width: 100%;
  border-radius: 10px;
  filter: opacity(0);
}